import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';
import $ from 'jquery';

import SeoulContainer from '../styles/SeoulContainer';

const Navb = styled.nav`
  position: fixed;
  background: ${props =>
    props.backgroundFixed ? props.theme.lightOrange : 'transparent'};
  width: ${props => `${props.width}px`};
  left: 0;
  top: 0;
  z-index: 100;
  transition: background 300ms;
  ul {
    display: flex;
    justify-content: center;
    li {
      padding: 0.5rem 1rem;
      display: flex;
      justify-content: center;
      align-items: center;
      &:first-child {
        margin-right: ${props => (props.mobile ? '0.5rem' : '1rem')};
      }

      a {
        font-size: 18px;
        text-transform: uppercase;
        font-weight: 500;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: color 300ms ease-in-out;
        color: ${props =>
          props.backgroundFixed ? props.theme.offWhite : '#f2f2f2'};

        div {
          display: flex;
          align-items: flex-end;
          & > *:nth-child(2) {
            margin-left: ${props => (props.mobile ? '5px' : '10px')};
          }
          img {
            width: auto;
            object-fit: cover;
          }
          img:nth-child(1) {
            height: ${props => (props.mobile ? '50px' : '60px')};
          }
          img:nth-child(2) {
            height: ${props => (props.mobile ? '30px' : '38px')};
          }
        }
      }
    }
  }
`;

const activeClassName = 'active';

const StyledNavLink = styled(NavLink).attrs({
  activeClassName: activeClassName
})`
  &.${activeClassName} {
    color: ${props => props.theme.darkRed};
  }
`;

class Navbar extends Component {
  constructor() {
    super();
    this.state = {
      fixed: false
    };
  }
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.fixed !== prevState.fixed) {
      return { fixed: nextProps.fixed };
    } else {
      return false;
    }
  }

  handleActive = () => {
    this.setState({ active: !this.state.active });
  };
  handleToTop = () => {
    // const wrapper = document.getElementsByClassName('wrapper')[0];
    // wrapper.scrollTo(0, 0);
    $('.wrapper').animate({ scrollTop: 0 }, 150);
  };
  render() {
    const { width, location } = this.props;
    const { fixed } = this.state;
    const mobile = width < 768 ? true : false;

    return (
      <Navb
        backgroundFixed={fixed}
        width={width}
        mobile={mobile}
        className="mainNav"
      >
        <SeoulContainer>
          <ul>
            <li>
              <NavLink
                to="/"
                onClick={e => {
                  if (location.pathname === '/') e.preventDefault();
                  else {
                    this.props.resetMenu(false);
                    this.handleToTop();
                  }
                }}
              >
                <div>
                  <img src="/images/logo-white-h60.png" alt="" />

                  <img src="/images/horizontal-white-h38.png" alt="" />
                </div>
              </NavLink>
            </li>
            <li>
              <StyledNavLink
                to="/menu"
                activeClassName="active"
                onClick={e => {
                  if (location.pathname.includes('/menu')) e.preventDefault();
                  else {
                    this.props.resetMenu(false);
                    this.handleToTop();
                  }
                }}
              >
                Menu
              </StyledNavLink>
            </li>
            {/* <li>
              <NavLink
                to="/catering"
                onClick={e => {
                  if (location.pathname.includes('/catering'))
                    e.preventDefault();
                  else {
                    this.props.resetMenu(false);
                    this.handleToTop();
                  }
                }}
              >
                Catering
              </NavLink>
            </li> */}
            <li>
              <StyledNavLink
                to="/contact"
                activeClassName="active"
                onClick={e => {
                  if (location.pathname === '/contact') e.preventDefault();
                  else {
                    this.props.resetMenu(false);
                    this.handleToTop();
                  }
                }}
              >
                Contact
              </StyledNavLink>
            </li>
          </ul>
        </SeoulContainer>
      </Navb>
    );
  }
}

export default Navbar;
